export function ProfileInfo(props) {
    const [user, setUser] = props.info

    const handleClick = e => {
        setUser(null)
    }

    return (
        <div className='profile-info'>
            <h3>{user.name}</h3>
            <p><a href={user.html_url}>@{user.login}</a></p>
            <button onClick={handleClick}>Delete</button>

            <div className='bio'>
                 <p>{user.bio}</p>
            </div>
        </div>
    )
}